<template>
	<!-- visible:模块显隐状态 before-close:关闭钱的回调，会暂停Drawer的关闭 -->
	<el-drawer title="我是标题" :visible="drawer" :before-close="handleClose">
			<span>我来啦!</span>
	</el-drawer>
</template>

<script>
	export default {
		data: function() {
			return {
			}
		},
		methods: {
			handleClose:function(){  //点击关闭。改变父组件drawer
				this.$emit('unDate:drawer',false)
			}
		},
		props: ['detailsData','drawer']
	}
</script>

